import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { Button } from 'react-bootstrap'
import '../../style.css'
import { userSlice } from '../../features/userSlice'
import { partnersSlice } from '../../features/partnersSlice'

const Pending = () => {
  const user = useSelector((state) => state.user.user)
  const navigate = useNavigate()
  const dispatch = useDispatch()
  return (
    <div>
      <div
        style={{ height: '100vh', backgroundColor: '#072541' }}
        className="text-white d-flex flex-column"
      >
        <nav
          className="w-100 bg-black h1 text-white d-flex h-100 align-items-center justify-content-center text-center text-md-start"
          style={{ minHeight: '5rem', maxHeight: '7rem' }}
        >
          Welcome{' '} <i className='text-success'>&quot;{user && user.name}&aquot;</i> {' '}You Account is pending for approval
        </nav>
        <main className="h-100 text-center fs-3 d-flex  flex-column justify-content-center">
          <p>We are evaluating your profile. This may take 1-2 business days.</p>
          <p>Once your profile is approved, you can access the site.</p>
          <p>Thank you for your patience.</p>
          <div className>
            <Button onClick={() => {
              navigate('/login')
              localStorage.clear()
              dispatch(userSlice.actions.clearUser())
              dispatch(partnersSlice.actions.clearPartners())
            }} className="myCustomBtn p-3">Logout</Button>
          </div>
          <div className='mt-3 '><Link className='text-decoration-none text-white' to={'https://adinath.site/contact-us/'} >Contact Us</Link></div>
        </main>
        <footer className="bg-black justify-self-end">
          <div className="container">
            <div className="row">
              <div className="col-12 text-center text-white py-3">
                <p className="mb-0">Â© 2021 All rights reserved.</p>
              </div>
            </div>
          </div>
        </footer>
      </div>
    </div>
  )
}

export default Pending
